import React from "react";
import Spinborder from "../quotepage/LoaderSpinner";

const CustomStyles = {
  top: "50%",
  left: "50%",
  position: "absolute"
};

const Spinner = () => {
  return (
    <div
      className="spinner-loader text-center position-fixed"
      style={{ width: "100vw", height: "100vh", zIndex: "9999" , top : "0" , left : "0" }}
    >
      <div
        className="position-absolute overlay-div"
        style={{ backgroundColor: "#000", width: "100vw", height: "100vh" }}
      />
      <Spinborder CustomStyles={CustomStyles} />
    </div>
  );
};

export default Spinner;
