import React from "react";

const Spinner = props => {
  return (
    <div
      className={`spinner-border text-warning ` + props.classo}
      style={{
        top: "16em",
        left: "40vw",
        width: "7em",
        height: "7em",
        position: "absolute",
        zIndex: "9991"
      }}
      role="status"
    >
      <span className="sr-only" />
    </div>
  );
};

export default Spinner;
