import axios from "axios";

const QuotesByRandom = axios.create({
  baseURL: "https://qvoca-bestquotes-v1.p.rapidapi.com",
  headers: {
    "X-RapidAPI-Host": "qvoca-bestquotes-v1.p.rapidapi.com",
    "X-RapidAPI-Key": "d17736776bmsh11903049c6ab86fp156be3jsn5d71ceb60ad4"
  }
});

export const QuotesByKeyword = keyword => {
  return axios.get(`https://150000-quotes.p.rapidapi.com/keyword/${keyword}`, {
    headers: {
      "X-RapidAPI-Host": "150000-quotes.p.rapidapi.com",
      "X-RapidAPI-Key": "d17736776bmsh11903049c6ab86fp156be3jsn5d71ceb60ad4"
    },
    params: {
      keyword
    }
  });
};

export default QuotesByRandom;
