import QuoteAPI from "../Api/QuoteAPI/QuoteAPI";
import Images from "../Api/PexelsAPI/PexelsAPI";

export const getrandomQuote = () => async dispatch => {
  let datao = "";
  const quotes = await QuoteAPI.get("/quote")
    .then(res => res.data)
    .then(data => (datao = data));
  setTimeout(
    () =>
      dispatch({
        type: "FETCHING_QUOTES",
        message: datao.message,
        author: datao.author
      }),
    2000
  );
};
export const getrandomImage = () => async dispatch => {
  let datao2 = "";

  const images = await Images()
    .then(res => res.data)
    .then(data => (datao2 = data));
  dispatch({
    type: "FETCHING_IMAGE",
    LSImage: datao2.photos[0].src.large2x
  });
};
