
import axios from 'axios';


const APIKEY = "563492ad6f9170000100000146d39b01ff6b4b758e9ef5a0fa86e703";
const RandomImage = Math.round(Math.random() * 1000);
 const ApiImage =  ()=>{
    return axios.get(`https://api.pexels.com/v1/search?query=Curated photos&per_page=1&page=${RandomImage}`,{
        headers : {
            Authorization : APIKEY
        }
    })
}

export default ApiImage;