import React from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import "./assets/sass/Main.scss";
import CopyAlert from "./CopiedAlert";
import { getrandomQuote, getrandomImage } from "../../Actions/Actions";
import { connect } from "react-redux";
import Spinner from "../Spinner/Spinner";
import Download from "downloadjs";
import html2canvas from "html2canvas";
import sound from "./assets/fxs/click.mp3";

class Quote extends React.Component {
  state = {
    quoteval: "",
    Copied: false,
    alertCopy: "",
    quote_: "",
    authorOfquote: "",
    dnone: "",
    imageUrl: "",
    loading: true
  };

  quoteRef = React.createRef();
  imgRef = React.createRef();

  testHandler = () => {
    if (!this.state.Copied) {
      this.setState({ Copied: true });
      setTimeout(() => {
        this.setState({ Copied: false });
      }, 3000);
    } else {
      this.setState({ Copied: false });
    }
  };

  checkinghas = () => {
    if (this.state.Copied) {
      return (
        <CopyAlert
          message={"quote are copied to clipboard"}
          className="dispo"
        />
      );
    } else {
      return "";
    }
  };

  dnoneTOspinner() {
    this.setState({ dnone: "d-none" });
  }

  getrandomQuote() {
    setTimeout(() => {
      const randomquote = this.props.APIS_DATA.quotes[0];
      const author = this.props.APIS_DATA.quotes[1];

      this.setState({ quote_: randomquote, authorOfquote: author });
    }, 2000);
  }

  getrandomimage() {
    setTimeout(() => {
      const randomImage = this.props.APIS_DATA.images;
      this.setState({ imageUrl: randomImage });
    }, 2000);
  }
  LoaderComponent() {
    if (this.state.loading) {
      return <Spinner />;
    } else {
      return " ";
    }
  }
  stopLoading() {
    window.onload = setTimeout(() => {
      this.setState({ loading: false });
    }, 4500);
  }
  componentDidMount = () => {
    this.setState({ val: this.quoteRef.current.textContent });
    this.getrandomQuote();
    setTimeout(() => this.dnoneTOspinner(), 2000);
    setTimeout(() => this.getrandomQuote(), 2000);
    setTimeout(() => this.getrandomimage(), 2000);
    this.stopLoading();
  };
  downloadImgOnclick() {
    let element = document.querySelector("#imgref");
    html2canvas(element, {
      useCORS: true,
      allowTaint: true
    }).then(canvas => {
      let cas = canvas
        .toDataURL("image/png")
        .replace("image/png", "image/octet-stream");
      Download(cas, "quoteimg.png");
    });
  }

  /*  

 */
  render() {
    const audito = () => {
      var soundONclick = new Audio(sound);
      soundONclick.play();
      soundONclick.currentTime = 0;
    };
    return (
      <div
        className={
          this.props.className +
          " p-0 background-badge col-lg-11 col-md-11 col-sm-10 d-flex justify-content-center align-items-center"
        }
      >
        {this.LoaderComponent()}
        <div className="container">
          <div className="col-auto quotes-container">
            <div
              id="imgref"
              className="img-container shadow position-relative"
              ref={this.imgRef}
              style={{
                minHeight: "400px",
                width: "auto",
                backgroundPosition: "center",
                backgroundSize: "cover",
                backgroundImage: `url(${this.state.imageUrl})`
              }}
            >
              <p
                ref={this.quoteRef}
                className={"lead position-absolute p-2"}
                style={{
                  bottom: "2em",
                  color: "#FFF",
                  fontSize: "1.25rem",
                  fontWeight: "bold"
                }}
              >
                {this.state.quote_}
                <br />
                <span> {this.state.authorOfquote} </span>
              </p>
              <div className="p-4">
                <ul className="nav">
                  <li
                    className="nav-item shadow"
                    title="copy"
                    onClick={this.testHandler}
                  >
                    <CopyToClipboard text={this.state.quote_}>
                      <i
                        className="fa fa-copy"
                        style={{ cursor: "pointer" }}
                        onClick={audito}
                      />
                    </CopyToClipboard>
                  </li>
                  <li
                    className="nav-item ml-3 shadow"
                    title="download"
                    onClick={this.downloadImgOnclick}
                  >
                    <i
                      className="fa fa-download"
                      style={{ cursor: "pointer" }}
                    />
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        {this.checkinghas()}
      </div>
    );
  }
}

const mapPropsToState = state => {
  return {
    APIS_DATA: state
  };
};
const todisp = dispatch => {
  return {
    bhlolo: dispatch(getrandomQuote()),
    bhqa: dispatch(getrandomImage())
  };
};
export default connect(
  mapPropsToState,
  todisp
)(Quote);
