import React, { Component } from "react";
import img from "./Assets/Imgs/branch-clear-deep.jpg";
import { Link } from "react-router-dom";
import "./Assets/Sass/Main.scss";
import Typed from "react-typed";

class LandPage extends Component {
  state = {
    navbarMarginBottom: "",
    ActivePage: ""
  };
  handleNavBarResponsive() {
    if (window.innerWidth < 767) {
      return this.setState({ navbarMarginBottom: " mb-3" });
    } else {
      return this.setState({ navbarMarginBottom: " mb-5" });
    }
  }

  getLocation() {
    return window.location.href;
  }

  breadCrumbDynamical() {
    if (this.getLocation() === "http://localhost:3000/") {
      return this.setState({ ActivePage: "active-home" });
    }
  }

  componentWillMount() {
    this.handleNavBarResponsive();
  }
  componentDidMount() {
    this.breadCrumbDynamical();
  }

  render() {
    return (
      <div className="col-lg-11 col-md-11 col-sm-12 landpage-main p-0" style={{backgroundImage:img}}>
        <div className="overlay-div">
          <div className="header p-4 block mx-auto d-flex flex-column align-content-center justify-content-center h-100">
            <div
              className="p-5 "
              style={{ color: "#fff", borderRadius: "2em" }}
            >
              <h1 className="display-3">quotime</h1>
              <p className="lead display-4" id="typed-text">
                <Typed
                  strings={["generate beautful quotes every second"]}
                  typeSpeed={40}
                />
              </p>
              <button className="btn btn-primary btn-lg my-3">
                {" "}
                <Link
                  style={{ color: "#FFF", textTransform: "uppercase" }}
                  to="/quotes"
                >
                  {" "}
                  start now
                </Link>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LandPage;
