import React from "react";
import { Link } from "react-router-dom";
import "./Assets/Sass/Navbar.scss";

class Navbar extends React.Component {
  state = {
    display: false
  };

  handleNavbarResponse() {
    if (window.innerWidth > 676) {
      return this.setState({ display: true });
    } else {
      return this.setState({ display: false });
    }
  }

  handleNavbarClass() {
    if (this.state.display) {
      return " ";
    } else {
      return "display-none-custom";
    }
  }

  componentDidMount() {
    this.handleNavbarResponse();
    this.handleNavbarClass();
  }

  render() {
    return (
      <nav
        className={
          `nav-left-hortizonal ` +
          this.props.className +
          " " +
          this.handleNavbarClass()
        }
        style={{ height: "100vh", backgroundColor: "#344973" }}
      >
        <ul className="nav flex-column align-items-center justify-content-center">
          <li className={"nav-item my-5 "}>
            <Link to="/">
              {" "}
              <i className="fa fa-home fa-2x" title="home" />{" "}
            </Link>
          </li>
          <li className={"nav-item my-5 "}>
            <Link to="/quotes">
              <i className="fa fa-quote-left fa-2x" title="quotes" />
            </Link>
          </li>
        </ul>
      </nav>
    );
  }
}

export default Navbar;
