import { combineReducers } from "redux";
import { imagesreducer } from "./images";
const reducer = (state = [], action) => {
  switch (action.type) {
    case "FETCHING_QUOTES":
      return [action.message, action.author];
      break;
    default:
      return state;
  }
};

export default combineReducers({
  quotes: reducer,
  images: imagesreducer
});
